@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500;700&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  background-color: #fff;
  color: #111;
}

.coinsContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 30px;
  color: #fff;
}

.header {
  display: flex;
  justify-content: space-around;
  /* padding-top: 10px; */

  background: #20242c;
  height: 70px;
  /* border: 2px solid red; */
  align-items: center;
  top: 0;
  position: -webkit-sticky;
  position: sticky;
  z-index: 3;
}

.brand {
  background: #20242c;
  color: #face2a;
}

.container {
  background: #20242c;
}

.inputField {
  /* border-radius: 15px; */
  padding: 0 10px 0 34px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  height: 40px;
  background-color: #fff;
  font-weight: bold;
  font-size: 16px;
}

.inputField:focus {
  /* background-color: #fff; */
  border: 1px solid #face2a;
  cursor: text;
  outline: 0;
  font-weight: bold;
}

.form {
  /* background: #20242c; */
  /* border-radius: 0px; */
}

@media (max-width: 520px) {
  .header {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  h1.brand {
    padding-bottom: 16px;
  }
}

.cryptoCoin {
  overflow: hidden;
  text-align: center;
  border-radius: 7px;
  position: relative;
  width: 320px;
  margin: 2rem;
  /* border: 1px solid red; */
  padding-top: 40px;
  background-color: #fff;
  box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048), 0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072), 0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12);
}

.coinLogo {
  width: 8rem;
  margin-top: 1.2rem;
  height: 40px;
  width: 40px;
  -webkit-clip-path: circle(60px at center);
          clip-path: circle(60px at center);
  /* padding-top: 40px; */
}

.coinNameWrap {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 4px;
  font-weight: 700;
}

.coinName {
  font-weight: bold;
  font-size: 1.5rem;
}

.coinSymbol {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  padding-left: 8px;
}

.priceContainerUP {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  border: none;
  font-size: 1rem;
  color: white;
  padding: 1rem;
  background-color: #127440;
}

.priceContainerDOWN {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  border: none;
  font-size: 1rem;
  color: white;
  padding: 1rem;
  background-color: #c4142a;
}

svg {
  background-color: transparent;
}

.priceChange {
  background-color: transparent;
  padding-left: 10px;
  color: #fff;
  /* text-shadow: 0 0 14px black; */
}

.coinPrice {
  width: auto;
  background-color: transparent;
}

.coinMarketcap {
  width: auto;
  padding-top: 8px;
  background-color: transparent;
}

.coinVolume {
  width: auto;
  padding-top: 6px;
  padding-bottom: 8px;
  background-color: transparent;
  padding-bottom: 70px;
}

